<template>
<div style="margin-bottom:20px;">
  <h4 class="ui horizontal divider header"> - {{index}} <a href="#" v-on:click.prevent="remove"></a></h4>
  <div class="two fields">
    <div class="field">
      <input type="text" v-bind:value="rule.timer" v-on:input="change('timer', $event.target.value)" placeholder=" * * * * *crontab "/>
    </div>
    <div class="field">
      <Dropdown title="" v-bind:items="nodeGroups" multiple="true"></Dropdown>
    </div>
  </div>
  <div class="field">
    <label></label>
    <Dropdown title="" v-bind:items="activityNodes" multiple="true"></Dropdown>
  </div>
  <div class="field">
    <label></label>
    <Dropdown title="" v-bind:items="activityNodes" multiple="true"></Dropdown>
  </div>
</div>
</template>

<script>
import Dropdown from './basic/Dropdown.vue';

export default {
  name: 'job-edit-rule',
  props: ['rule', 'index'],
  data: function(){
    return {
      nodeGroups: [],
      activityNodes: []
    }
  },

  mounted: function(){
    var vm = this;
    this.$rest.GET('node/activitys').onsucceed(200, (resp)=>{vm.activityNodes = resp}).do();
    this.$rest.GET('node/groups').onsucceed(200, (resp)=>{
      var groups = [];
      for (var i in resp) {
        groups.push({value: resp[i].id, name: resp[i].name});
      }
      vm.nodeGroups = groups;
    });
  },

  methods: {
    remove: function(){
      this.$emit('remove', this.index);
    },
    change: function(key, val){
      this.$emit('change', this.index, key, val);
    }
  },

  components: {
    Dropdown
  }
}
</script>


